import withRoot from "../../utils/withRoot";
import React from "react";
import { graphql } from "gatsby";
import SEO from "../../components/SEO";
import Page from "../../components/Page";
import List from "../../components/List";

const Tutorial = props => {
    const tutorial = props.data.allMarkdownRemark.edges;
    return (
        <Page title="Tutorial">
            <SEO title="Tutorial" />
            <List items={tutorial} />
        </Page>
    );
};
//TODO:: tutaj jest style odwolywania sie do pod katalogow
export const query = graphql`
  query TutorialsQuery {
    allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "/tutorial/content/" } }
      sort: { fields: [frontmatter___date], order: DESC }
    ) {
      edges {
        node {
          excerpt
          frontmatter {
            image {
              publicURL
            }
            title
            path
          }
        }
      }
    }
  }
`;

export default withRoot(Tutorial);
